body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --youtube-frame-width: 1024px;
  --youtube-frame-height: 578px;
}
@media only screen and (max-width: 1024px) {
  :root {
    --youtube-frame-width: 853px;
    --youtube-frame-height: 505px;
  }
}
@media only screen and (max-width: 860px) {
  :root {
    --youtube-frame-width: 640px;
    --youtube-frame-height: 385px;
  }
}
@media only screen and (max-width: 650px) {
  :root {
    --youtube-frame-width: 480px;
    --youtube-frame-height: 270px;
  }
}

.jumbotron {
  padding-top: 3rem;
  padding-bottom: 3rem;
  margin-bottom: 0;
  min-height: 300px;
  background-image: url(/hexapod-web/static/media/jumbotron-bg.6519e2ed.jpg); 
  background-size: 100%; 
  background-repeat: no-repeat;
}
@media (orientation: portrait) {
  .jumbotron {
    padding-top: 1rem;
    padding-bottom: 1rem;
    min-height: 200px;
  }
}

.text-outlined {
  color: black;
  text-shadow:
    -1px -1px 0 white,  
    1px -1px 0 white,
    -1px 1px 0 white,
    1px 1px 0 white;
}

.jumbotron-heading {
  font-size: 3rem;
  font-weight: 700;
}
.jumbotron-text {
  font-size: 1.2rem;
  max-width: 30rem;
}
@media (orientation: portrait) {
  .jumbotron-heading {
    font-size: 2rem;
    font-weight: 700;
  }
  .jumbotron-text {
    font-size: 1rem;
    max-width: 30rem;
  }
}

.jumbotron p:last-child {
  margin-bottom: 0;
}

.jumbotron p a {
  margin-left: 2px;
}

.badge {
  margin-right: 2px;
}

.youtube-dialog {
  width: 1024px;
  width: var(--youtube-frame-width);
  max-width: none!important;
}

.youtube-wrapper {
  width: 1024px;
  width: var(--youtube-frame-width);
  height: 578px;
  height: var(--youtube-frame-height);
}

.youtube-iframe {
  width: 1024px;
  width: var(--youtube-frame-width);
  height: 578px;
  height: var(--youtube-frame-height);
}

footer {
  padding-top: 3rem;
  padding-bottom: 3rem;
  background-color: #EEE;
}

footer p {
  margin-bottom: .25rem;
}

.card-img-bg {
  background-color: #000;
}

.fade { /* Safari */
  transition-duration: 0.5s;
}

